
import headTop from '@/components/header'
import ViewBox from 'vux/src/components/view-box/index.vue'

import { mapGetters, mapMutations } from 'vuex'
import Api from '@/js/common'
import single from '@/pages/questionType/single'
import double from '@/pages/questionType/double'
import downSelect from '@/pages/questionType/downSelect'
import singleLine from '@/pages/questionType/singleLine'
import multiLine from '@/pages/questionType/multiLine'

export default {
data() {
return {
title: '',
discription:'',
endWord: '',
endTime: '',
questionList: []
}
},
computed: {
...mapGetters(['device'])
},
methods: {
creatFormatCode(strValue){
return strValue.replace(/\r\n/g, '<br/>').replace(/\n/g, '<br/>').replace(/\s/g, '&nbsp;');
},
getFormatCode(strValue){
return strValue.replace(/<br\/>/g, "\n").replace(/&nbsp;/g," ");
}
},
mounted() {
appcan.setNavigationBarTitle({
title: "title"
});
},
components: {
ViewBox,
headTop,
single,
double,
downSelect,
singleLine,
multiLine
},
created() {
var info = this.$route.params.quesInfo
this.title = info.title
this.discription = info.discription
this.endWord = info.endWord
this.endTime = info.endTime
this.questionList = info.questionList
}
}



